import React from 'react';

import FilterLink from './FilterLink/FilterLink';
import { SHOW_ALL, SHOW_ACTIVE, SHOW_COMPLETED} from '../filterList';

const Footer = () => {
  return (
    <p>
      Show:
      {' '}
      <FilterLink
        filter={SHOW_ALL}
      >
        All
      </FilterLink>
      {' | '}
      <FilterLink
        filter={SHOW_ACTIVE}
      >
        Active
      </FilterLink>
      {' | '}
      <FilterLink
        filter={SHOW_COMPLETED}
      >
        Completed
      </FilterLink>
    </p>
  );
};

export default Footer;



